@charset "UTF-8";
@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("ArialRegular"), url(./fonts/ArialRegular.ttf) format("truetype");
}
@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local("ArialBold"), url(./fonts/ArialBold.ttf) format("truetype");
}
@font-face {
  font-family: "Lora";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local("LoraBold"), url(./fonts/LoraBold.ttf) format("truetype");
}
@font-face {
  font-family: "Lora";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("LoraRegular"), url(./fonts/LoraRegular.ttf) format("truetype");
}
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  line-height: 20px;
  background: #fff;
  background-size: 1920px auto;
  position: relative;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

html {
  scroll-behavior: smooth;
}

