.modal.modalActive {
  z-index: 10;
  opacity: 1;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #80b500;
  z-index: -10;
  opacity: 0;
  transition: opacity 0.5s;
}
.modal .container1170px {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
}
@media screen and (max-width: 480px) {
  .modal .container1170px {
    padding: 0 5px;
  }
}
.modal__wrapper {
  position: relative;
  max-width: 1170px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 100px;
}
.modal__close {
  position: absolute;
  right: 0;
  top: -45px;
  width: 40px;
  height: 40px;
  opacity: 0.5;
  cursor: pointer;
  transition: 0.2s;
}
.modal__close:before {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  width: 30px;
  height: 2px;
  background-color: black;
  transform: translate(-50%, -50%) rotate(45deg);
}
.modal__close:after {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  width: 30px;
  height: 2px;
  background-color: black;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.modal__close:hover {
  opacity: 0.8;
}
.modal__title {
  text-align: center;
  font-weight: bold;
  font-family: "Lora", serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #fff;
  font-size: 30px;
  line-height: 30px;
  padding-bottom: 15px;
}
@media screen and (max-width: 940px) {
  .modal__title {
    line-height: 17px;
    letter-spacing: inherit;
    font-size: 15px;
    padding-top: 50px;
  }
}
.modal__form {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.modal__form input {
  height: 55px;
  padding: 10px 10px 5px;
  color: #555a5f;
  font-size: 18px;
  border-left: 1px solid #e5e7e8;
  border-right: 1px solid #e5e7e8;
  border-bottom: thin solid #f3f3f4;
  border-top: 3px solid #a2a7ac;
  border-radius: 6px;
  background: #f2f5f8;
  box-sizing: border-box;
  flex: 0 0 30%;
}
@media screen and (max-width: 940px) {
  .modal__form {
    margin-top: 20px;
    flex-direction: column;
  }
  .modal__form > *:nth-child(n) {
    margin-bottom: 10px;
  }
  .modal__form input {
    width: 100%;
    flex: auto;
    height: 45px;
  }
}
.modal__submit {
  padding: 0 45px;
  background: linear-gradient(#92cf00, #6e9c00);
  border-radius: 5px;
  border-bottom: 5px solid #5c8200;
  cursor: pointer;
  color: #fff;
  line-height: 57px;
  font-size: 18px;
  font-family: "Arial", sans-serif;
  text-transform: uppercase;
  text-align: center;
  color: #313437;
  border-bottom-color: #a38901;
  background: linear-gradient(#fddc26, #bc9f01);
}
.modal__submit:hover {
  text-shadow: 0 -1px 1px #313437;
  color: #e1e8ef;
  background: linear-gradient(#efca02, #d5b402);
}
@media screen and (max-width: 940px) {
  .modal__submit {
    margin-top: 30px;
  }
}

.bodyActiv {
  overflow: hidden;
